export const EMPTY_OBJ = {};
export const EMPTY_ARR = [];
export const noop = () => { };
export function toCamelCase(s) {
    let camel = '';
    let nextCap = false;
    for (let i = 0; i < s.length; i++) {
        if (s[i] === '-') {
            nextCap = true;
        }
        else {
            camel += nextCap ? s[i].toUpperCase() : s[i];
            nextCap = false;
        }
    }
    return camel;
}
export function toDashed(s) {
    return s.replace(/([a-z0-9])([A-Z])/g, '$1-$2').toLowerCase();
}
export const toKebabCase = function (string) {
    return string.replace(/([a-z])([A-Z])/g, '$1-$2').toLowerCase();
};
export function capitalize(s) {
    return s.charAt(0).toUpperCase() + s.slice(1);
}
export function indent(str, size) {
    return str.split('\n')
        .map((line, index) => {
        const indent = index === 0 ? '' : Array(size).fill(' ').join('');
        return indent + line;
    })
        .join('\n');
}
/**
 * ensure takes a condition and throw a error if the condition fails,
 * like failure::ensure: https://docs.rs/failure/0.1.1/failure/macro.ensure.html
 * @param condition condition.
 * @param msg error message.
 */
export function ensure(condition, msg) {
    if (!condition) {
        if (process.env.NODE_ENV === 'production') {
            throw new Error(msg);
        }
        else {
            const reportIssue = '\n issue https://github.com/alibaba/ice/issues';
            throw new Error(msg + reportIssue);
        }
    }
}
export function warn(condition, msg) {
    if (process.env.NODE_ENV !== 'production') {
        if (condition) {
            console.warn(`[ice miniapp warn] ${msg}`);
        }
    }
}
const { hasOwnProperty } = Object.prototype;
export const hasOwn = (val, key) => hasOwnProperty.call(val, key);
