import { controlledComponent, isUndefined } from '@ice/shared';
import { COMMENT, DOCUMENT_ELEMENT_NAME, ROOT_STR, } from '../constants/index.js';
import { Element } from '../dom/element.js';
import { createEvent } from '../dom/event.js';
import { eventSource } from '../dom/event-source.js';
import { FormElement } from '../dom/form.js';
import { RootElement } from '../dom/root.js';
import { Text } from '../dom/text.js';
import env from '../env.js';
export class Document extends Element {
    constructor() {
        super();
        this.createEvent = createEvent;
        this.nodeType = 9 /* NodeType.DOCUMENT_NODE */;
        this.nodeName = DOCUMENT_ELEMENT_NAME;
    }
    createElement(type) {
        if (type === ROOT_STR) {
            return new RootElement();
        }
        const element = controlledComponent.has(type)
            ? new FormElement()
            : new Element();
        element.nodeName = type;
        element.tagName = type.toUpperCase();
        return element;
    }
    // an ugly fake createElementNS to deal with @vue/runtime-dom's
    // support mounting app to svg container since vue@3.0.8
    createElementNS(_svgNS, type) {
        return this.createElement(type);
    }
    createTextNode(text) {
        return new Text(text);
    }
    getElementById(id) {
        const el = eventSource.get(id);
        return isUndefined(el) ? null : el;
    }
    querySelector(query) {
        //  Vue3 
        if (/^#/.test(query)) {
            return this.getElementById(query.slice(1));
        }
        return null;
    }
    querySelectorAll() {
        // fake hack
        return [];
    }
    // @TODO: @PERF:  hydrate  node
    createComment() {
        const textnode = new Text('');
        textnode.nodeName = COMMENT;
        return textnode;
    }
    get defaultView() {
        return env.window;
    }
}
